.table > tbody > :not(:first-child) {
    border-top: 2px solid #b1b1b148;
}
.table > tbody {
    color: #6E6E6E
}
.table {
    background-color: #FFFFFF;
    border-radius: 4px;
    box-shadow: 2px 2px 5px rgba(0,0,0,0.1);
    border-collapse: separate;
    border-spacing: 7px 15px;
}
.table > thead {
    font-family: RobotoMedium;
    font-size: 14px
}

.table > tbody > tr > td {
    padding-right: 50px;
}