
.navigation {
    width: 256px;
    height: 100vh;
    position: relative;
    padding: 0.5rem 0rem;
    background-color: #FFFFFF;
    color: #7A7B7B;
}
.navigation_menu {
    margin-right: auto;
    margin-top: 43px;
}
.navigation_menu ul {
    padding: 0;
}
.navMenu > div {
  height: 49px;
}
.navMenu > div > a > span {
  margin-left: 20px;
  font-family: RobotoMedium;
  font-size: 14px;
}
.navigation_menu li {
    list-style-type: none;
    margin: 0 1rem;
}
.navigation_menu li a {
    text-decoration: none;
    display: block;
    width: 100%;
}
.hamburger {
    border: 0;
    height: 40px;
    width: 40px;
    padding: 0.5rem;
    border-radius: 50%;
    background-color: #283b8b;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
    position: absolute;
    top: 50%;
    right: 25px;
    transform: translateY(-50%);
    display: none;
  }
  .hamburger:hover {
    background-color: #2642af;
  }
  @media screen and (max-width: 768px) {
    .hamburger {
      display: block;
    }
  }
  @media screen and (max-width: 768px) {
    .navigation_menu ul {
      display: none;
    }
  }
  @media screen and (max-width: 768px) {
    .navigation_menu ul {
      position: absolute;
      top: 60px;
      left: 0;
      flex-direction: column;
      width: 100%;
      height: calc(100vh - 77px);
      background-color: white;
      border-top: 1px solid black;
    }
    .navigation_menu li {
      text-align: center;
      margin: 0;
    }
    .navigation_menu li a {
      color: black;
      width: 100%;
      padding: 1.5rem 0;
    }
    .navigation_menu li:hover {
      background-color: #eee;
    }
  }
  @media screen and (max-width: 768px) {
    .navigation_menu ul {
      /* previous styles */
      display: none;
    }
  }
  @media screen and (max-width: 768px) {
    .navigation_menu.expanded ul {
      display: block;
    }
  }


